import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import "./Detail.scss";

let 박스 = styled.div`
  padding: 20px;
`;

let 제목 = styled.h4`
  font-size: 25px;
  color: ${(props) => props.색상};
`;

function Detail(props) {

  let { id } = useParams();
  let history = useHistory();
  let [alert, alert변경] = useState(true);
  let [inputData, inputData변경] = useState('');


  useEffect(() => {
    // 2초 후에 alert 창 안 보이게 해라
    let 타이머 = setTimeout(() => {alert변경(false)}, 2000);
    console.log("hi")
    return ()=>{
      // 타이머 해제. 안해주면 나중에 문제 발생 가능
      clearTimeout(타이머);
    }
  },[alert]);

  
  const detail_shoe = props.shoes.find((i) => {
    return i.id == id;
  });

  return (
    <div className="container">
      <박스>
        <제목 className="red">Detail</제목>
      </박스>

      <input onChange={(e)=>{inputData변경(e.target.value)}}/>
      { inputData }

      {alert ? (
        <div className="my-alert2">
          <p>재고가 얼마 남지 않았습니다.</p>
        </div>
      ) : null}

      <div className="row">
        <div className="col-md-6">
          <img
            src={
              "https://codingapple1.github.io/shop/shoes" +
              (detail_shoe.id + 1) +
              ".jpg"
            }
            width="100%"
          />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{detail_shoe.title}</h4>
          <p>{detail_shoe.content}</p>
          <p>{detail_shoe.price}원</p>
          <Info 재고={props.재고}/>
          <button className="btn btn-danger" onClick={()=>{ props.재고변경(props.재고-1) }}>주문하기</button>
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}

function Info(props){
  return(
    <p>재고 : {props.재고[0]}</p>
  )
}

export default Detail;
